/**
 * Ëé∑ÂèñÊó•ÂéÜÂ±ïÁ§∫Êó•ÊúüÂàóË°®
 *
 * @export
 * @param {Number} y
 * @param {Number} m
 * @returns
 */
export function getDateList(y, m) {
  var year = y;
  var month = m - 1;
  var list = [];
  var now = new Date(year, month);
  var monthEnd = new Date(year, month + 1, 0); // ÂΩìÊúàÊúÄÂêé‰∏ÄÂ§©
  var lastMonthEnd = new Date(year, month, 0); // ‰∏äÊúàÊúÄÂêé‰∏ÄÂ§©
  var firstDay = now.getDay(); // ÂΩìÊúàÁ¨¨‰∏ÄÂ§©
  var mEDate = monthEnd.getDate();
  var lMEDate = lastMonthEnd.getDate();

  // ËÆ°ÁÆó‰∏äÊúàÂá∫Áé∞Âú® üìÖ ‰∏≠ÁöÑÊó•Êúü
  for (var i = 0; i < firstDay; i++) {
    var tempM = month > 0 ? month : 12;
    var tempY = month > 0 ? year : year - 1;
    var strMonth = tempM < 10 ? '0' + tempM : tempM;
    list.unshift(tempY + '-' + strMonth + '-' + (lMEDate - i));
  }

  // ÂΩìÊúà
  for (var _i = 1; _i < mEDate + 1; _i++) {
    var strI = _i < 10 ? '0' + _i : _i;
    var _tempM = month + 1;
    var _strMonth = _tempM < 10 ? '0' + _tempM : _tempM;
    list.push(year + '-' + _strMonth + '-' + strI);
  }

  var tempLen = 42 - list.length;

  // ‰∏ãÊúà
  for (var _i2 = 1; _i2 < tempLen + 1; _i2++) {
    var _strI = _i2 < 10 ? '0' + _i2 : _i2;
    var _tempM2 = month + 2 < 13 ? month + 2 : 1;
    var _tempY = month + 2 < 13 ? year : year + 1;
    var _strMonth2 = _tempM2 < 10 ? '0' + _tempM2 : '' + _tempM2;
    list.push(_tempY + '-' + _strMonth2 + '-' + _strI);
  }

  return list;
}

/**
 * @name convertDyadicArray ‰∏ÄÁª¥Êï∞ÁªÑËΩ¨Êç¢‰∏∫‰∫åÁª¥Êï∞ÁªÑ
 * @param {Array} arr
 * @param {Number} row
 * @author Sven
 * @example convertDyadicArray([2,3,4,5,6,7], 3) => [[2,3],[4,5],[6,7]]
 */
export function convertDyadicArray(arr, row) {
  var dyadicArray = [];
  var col = arr.length / row;
  for (var i = 0; i < row; i++) {
    dyadicArray.push(arr.slice(i * col, (i + 1) * col));
  }
  return dyadicArray;
}

/**
 * Êó•ÊúüÊ†ºÂºèÂåñ 'YYYY-MM-DD'
 * 
 * @export
 * @param {any} date 
 * @returns 
 */
export function dateFormat(date) {
  var year = date.getFullYear();
  var month = date.getMonth() + 1;
  var day = date.getDate();
  var monthStr = month < 10 ? '0' + month : '' + month;
  var dayStr = day < 10 ? '0' + day : '' + day;

  return year + '-' + monthStr + '-' + dayStr;
}